import React from 'react';
import "../App.css";
import {NavLink} from "react-router-dom";

const NavBar = () => {
    return(
        <>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-11 mx-auto">
                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                        <div className="container-fluid">
                            <h4><b className="O">O</b>perating <b className="O">S</b>ystem</h4>
                            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                                <li className="nav-item">
                                <NavLink exact activeClassName="active-menu" className="nav-link active" aria-current="page" to="/">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                <NavLink exact activeClassName="active-menu" className="nav-link" to="/about">About</NavLink>
                                </li>
                                <li className="nav-item">
                                <NavLink exact activeClassName="active-menu" className="nav-link" to="/register">Register</NavLink>
                                </li>
                            </ul>
                            </div>
                        </div>
                        </nav>
                    </div>
                </div>
            </div>
        </>
    );
}

export default NavBar;